import VueRouter from "vue-router";
import Vue from "vue";
import Nprogress from "nprogress";
import 'nprogress/nprogress.css'

Vue.use(VueRouter); //使用第三方插件，必须use

const Login = () => import("@/views/Login/Login.vue")
const Layout = () => import("@/Layout/Index")
const User = () => import("@/views/user/index.vue")
const Position = () => import("@/views/user/Position.vue")
const Role = () => import("@/views/user/Role.vue")
const Message = () => import("@/views/commodity/Message.vue")
const Number = () => import("@/views/commodity/Number.vue")
const Profile = () => import("@/views/owner/Profile.vue")
const Home = () => import("@/views/Home/Index.vue")
const Journal = () => import("@/views/journal/Journal.vue")


const routes = [
    {
        path: "/login",
        component: Login,
    },
    {
        path: "/",
        component: Layout,
        children: [
            {
                path: "",
                component:Home
            }
        ]
    },
    {
        path: "/journal",
        component: Layout,
        children: [
            {
                path: "",
                redirect: "profile",
            },
            {
                path: "journal",
                component:  Journal
            }
        ]
    },
    {
        path: "/user",
        component: Layout,
        children: [
            {
                path: "", 
                redirect: "user", 
            },
            {
                path: "user",
                component: User,
            },
            {
                path: "position",
                component: Position,
            },
            {
                path: "role",
                component: Role
            },
          
        ]
    },
    {
        path: "/owner",
        component: Layout,
        children: [
            {
                path: "",
                redirect: "profile",
            },
            {
                path: "profile",
                component: Profile
            },
        ]
    },
    {
        path: "/commodity",
        component: Layout,
        children: [
            {
                path: "",
                redirect: "Message",
            },
            {
                path: "Message",
                component: Message
            },
            {
                path: "Number",
                component: Number
            },
        ]
    },
 
]
let router = new VueRouter({
    mode: "hash",
    routes
})

router.beforeEach((to, from, next) => {
    next()
})

export default router
